import React, { useState, useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { UserContext } from '../userPages/UserContext';


function Login () {
  const navigate = useNavigate();
  const { setCurrentUser } = useContext(UserContext);
  // const [formErrors, setFormErrors] = useState([]);
  const [userLoginData, setUserLoginData]= useState({
    username: '',
    password: '',
  });
  
  const { username, password } = userLoginData;

  const handleSubmit = async (evt) => {
    evt.preventDefault();
    try {
      const res = await axios.post('http://localhost:5000/auth/login', userLoginData);
      console.log('Submitting Form data', userLoginData);
      console.log(res.data);
      localStorage.setItem('token', res.data.jwtToken)
      setCurrentUser(res.data.user);
      console.log(setCurrentUser(res.data));
      navigate('/dashboard')
    } catch (error) {
      console.error('Error logginig in:', error);
      // setFormErrors([error.message])
    }
  };

  function handleChange(evt) {
    const { name, value } = evt.target;
    setUserLoginData(prevState => ({ ...prevState, [name]: value }))
  }
  
  return (
  <div className='LoginForm'>
  <div className='container col-md-6 offset-md-3 col-lg-4 offset-lg-4'>
    

    <div className='card'>
      <h2 className='text-center mb-3'> Login </h2>
      <div className='card-body'>
        <form onSubmit={handleSubmit}>

          <div className='form-group'>
            <label htmlFor='username'> Username </label>
            <input 
              name='username'
              className='form-control'
              value={username}
              onChange={handleChange}
              autoComplete='username'
              required
            />
          </div>

          <div className='form-group'>
            <label htmlFor='password'> Password </label>
            <input 
              type='password'
              name='password'
              className='form-control'
              value={password}
              onChange={handleChange}
              required
            />
          </div>

          {/* {formErrors.length
              ? <Alert type='danger' messages={formErrors} />
              : null}  */}

            <button 
                  type='submit'
                  className='btn btn-primary float-right'
                  onSubmit={handleSubmit}>
            Submit
          </button>
        </form>

          
      </div>
    </div>
  </div>
</div>
)
}

export default Login;