import React from 'react';
import { Link } from 'react-router-dom';
import armsImage from '../images/armsImage.jpg';
import backImage from '../images/backImage.jpg';
import cardioImage from '../images/cardioImage.jpg';
import chestImage from '../images/chestImage.jpg';
import lowerLegsImage from '../images/lowerLegsImage.jpg';
import shouldersImage from '../images/shouldersImage.jpg';
import neckImage from '../images/neckImage.jpg';




function AllExercies() {
  console.log('armsImage:', armsImage);
  return (
    <div> 
      <h6 class='display-6'> Choose Your Body Part </h6>
        <Link to='/arms'> 
          <img src={armsImage} alt='Arms'></img> 
        </Link>
        
        <Link to='/back'> 
          <img src={backImage} alt='Back'></img> 
        </Link>

        <Link to='/cardio'> 
          <img src={cardioImage} alt='Cardio'></img> 
        </Link>

        <Link to='/chest'> 
          <img src={chestImage} alt='Chest'></img> 
        </Link>

        <Link to='/lowerlegs'> 
          <img src={lowerLegsImage} alt='Lower Legs'></img> 
        </Link>
        <Link to='/neck'> 
          <img src={neckImage} alt='Neck'></img> 
        </Link>
        <Link to='/shoulders'> 
          <img src={shouldersImage} alt='Shoulders'></img> 
        </Link>
    </div>
  )
}

export default AllExercies;