import React, { useContext } from 'react';
import { Link, useNavigate } from 'react-router-dom';
// import './Navbar.css';
import { UserContext } from '../userPages/UserContext';
import 'bootstrap/dist/css/bootstrap.min.css';

function Navbar() {
  const { currentUser, setCurrentUser }= useContext(UserContext);
  const navigate = useNavigate();

  const handleLogout = () => {
    setCurrentUser(null);
    localStorage.removeItem('token');
    navigate('/login');
  }

  return (
    <nav className='navbar bg-dark border-bottom border-body' data-bs-theme='dark'>
      <nav className='navbar navbar-expand-lg bg-body-tertiary'>
        <div className='container-fluid'>
          <Link className='navbar-brand' href='/'> <b> Get Fit </b> </Link>
          <div className='collapse navbar-collapse' id='navbarSupportedContent'>
            <ul className='navbar-nav me-auto mb-2 mb-lg-0'>

              {!currentUser ? (
                <>
                  <li className='nav-item'>
                    <Link className='nav-link' to='signup'> Signup </Link>
                  </li>
                  <li className='nav-item'>
                    <Link className='nav-link' to='/login'> Login </Link>
                  </li>
                </>
              ) : (

                <>
                  <li className='nav-item'>
                    <Link className='nav-link' to='/bmi'> BMI </Link>
                  </li>
                  <li className='nav-item'>
                    <Link className='nav-link' to='/calendar'> Calendar </Link>
                  </li>
                  <li className='nav-item'>
                    <Link className='nav-link' to='/allExercises'> Exercises </Link>
                  </li>
                </>
              )}

              <li className='nav-item'>
                <Link className='nav-link' to='/cardioInfo'> Cardio Info </Link>
              </li>
              <li className='nav-item'>
                <Link className='nav-link' to='/lifeChanges'> Life Changes </Link>
              </li>
              </ul>

              {currentUser ? (
                <div className='d-flex align-items-center'> 
                  <span className='navbar-text me-3'> {currentUser.username} </span>
                  <button 
                      className='btn btn-outline-light' 
                      onClick={handleLogout}> 
                    Logout 
                  </button>
                </div>
              ): null}

          </div>    
        </div>
      </nav>
     </nav> 
  )
}

export default Navbar;