import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
// import Alert from "../common/Alert";
import axios from 'axios';
// import GetFit from '../api/GetFit';

function Register() {

  const navigate = useNavigate();
  // const [formErrors, setFormErrors] = useState([]);
  const [userRegisterData, setUserRegisterData] = useState({
    username: "",
    password: "",
    firstName: "",
    lastName: "",
    email: "",
  });
  
  const { username, password, firstName, lastName, email } = userRegisterData;

  async function handleSubmit(evt) {
    evt.preventDefault();
    try{
      const res= await axios.post('http://localhost:5000/auth/register', userRegisterData);
      console.log('Submitting form data', userRegisterData)
      console.log(res.data)
      localStorage.setItem('token', res.data.jwtToken);
      navigate('/login')
    } catch (error) {
      console.error('Error registering user:', error);
      // setFormErrors([error.response.data]);
    }
  }

  /** Update form data field */
  function handleChange(evt) {
    const { name, value } = evt.target;
    setUserRegisterData((prevData)=> ({ ...prevData, [name]: value }));
  }

  return (
      <div className="SignupForm">
        <div className="container col-md-6 offset-md-3 col-lg-4 offset-lg-4">

          <div className="card">
            <h2 className="text-center mb-3"> Register </h2>
            <div className="card-body">
              <form 
                    // type='button' 
                    // className='btn btn-primary float-right'
                    onSubmit={handleSubmit}>
                <div className="form-group">
                  <label htmlFor='username'> Username </label>
                  <input
                      name="username"
                      className="form-control"
                      value={username}
                      onChange={handleChange}
                  />
                </div>
                <div className="form-group">
                  <label htmlFor='password'> Password </label>
                  <input
                      type="password"
                      name="password"
                      className="form-control"
                      value={password}
                      onChange={handleChange}
                      required
                  />
                </div>

                <div className="form-group">
                  <label htmlFor='firstName'> First name </label>
                  <input
                      name="firstName"
                      className="form-control"
                      value={firstName}
                      onChange={handleChange}
                  />
                </div>
                <div className="form-group">
                  <label htmlFor='lastName'> Last name </label>
                  <input
                      name="lastName"
                      className="form-control"
                      value={lastName}
                      onChange={handleChange}
                  />
                </div>
                <div className="form-group">
                  <label htmlFor='email'> Email </label>
                  <input
                      type="email"
                      name="email"
                      className="form-control"
                      value={email}
                      onChange={handleChange}
                  />
                </div>

                {/* {formErrors.length
                    ? <Alert type="danger" messages={formErrors} />
                    : null
                }  */}

                <button
                    type="submit"
                    className="btn btn-primary float-right"
                    onSubmit={handleSubmit}
                >
                  Register
                </button>
              </form>

              
            </div>
          </div>
        </div>
      </div>
  );
}

export default Register;