import React, { useContext } from 'react';
import { Route, Routes as ReactRouter } from 'react-router-dom';
import Register from '../components/Register';
import Login from '../components/Login';
import BMI from '../userPages/BMI';
import Home from '../components/Homepage';
import Navbar from './Navbar';
import Dashboard from '../components/Dashboard';
import AllExercises from '../exercises/AllExercises'; 
import Cardio from '../exercises/Cardio';
import Arms from '../exercises/Arms';
import Back from '../exercises/Back';
import Chest from '../exercises/Chest';
import LowerLegs from '../exercises/LowerLegs';
import Neck from '../exercises/Neck';
import Shoulders from '../exercises/Shoulders';
import UpperArms from '../exercises/UpperArms';

// import LifeChanges from '../guestPages/LifeChanges';
import Calendar from '../userPages/Calendar';
import { UserContext } from '../userPages/UserContext';

function Routes() {

  const { currentUser } = useContext(UserContext);

  return (
    <div className='pt-5'>
        <Navbar />
        <ReactRouter> 
          { currentUser ? (
            <>
              <Route exact path='/dashboard' element={<Dashboard />}/>
              <Route exact path='/bmi' element={ <BMI /> }/> 
              <Route exact path='/calendar' element={ <Calendar /> }/>
              <Route exact path='/cardio' element={ <Cardio /> }/>
              <Route exact path='/arms' element={ <Arms /> }/>
              <Route exact path='/back' element={ <Back /> }/>
              <Route exact path='/lowerlegs' element={ <LowerLegs /> }/>
              <Route exact path='/neck' element={ <Neck /> }/>
              <Route exact path='/shoulders' element={ <Shoulders /> }/>
              <Route exact path='/chest' element={ <Chest /> }/>
              <Route exact path='/allExercises' element={ <AllExercises /> }/>
              <Route exact path='/upperArms' element={ <UpperArms /> }/>
            </>

            ) : (
            
            <>
              <Route exact path='/' element={<Home />}/>
              <Route exact path='/signup' element={<Register />}/>
              <Route exact path='/login' element={<Login />}/>
              {/* <Route exact path='/cardioInfo' element={<CardioInfo />}/>  */}
              {/* <Route exact path='/lifechanges' element={<LifeChanges />}/>  */}
        </>
      )
    }
        </ReactRouter>
    </div>
  )
}

export default Routes;