import React, { useState, useEffect } from 'react';
import GetExercisesFromDb from './GetExercisesFromDb';
import ExpandableCard from './ExpandableCard';
import './styles/Back.css';

function Chest () {

  const [exercisesByTarget, setExercisesByTarget] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const cardsPerTarget = 3; 
  const [currentIndexByTarget, setCurrentIndexByTarget] = useState({});

  useEffect(() => {
    const bodyPart = 'chest';
    GetExercisesFromDb(bodyPart)
      .then(exercises => {
        const groupedExercises = groupExercisesByTarget(exercises);
        setExercisesByTarget(groupedExercises);
        setLoading(false);
        initializeIndexByTarget(groupedExercises);
        console.log('Exercises:', groupedExercises);
      })
      .catch(err => {
        setError(err);
        setLoading(false);
        console.error('Error getting back exercises', err);
      });
  }, []);

  function groupExercisesByTarget(exercises) {
    const groupedExercises = {};
    exercises.forEach(exercise => {
      if (!groupedExercises[exercise.target]) {
        groupedExercises[exercise.target] = [];
      }
      groupedExercises[exercise.target].push(exercise);
    });
    return groupedExercises;
  }

  function initializeIndexByTarget(groupedExercises) {
    const indexByTarget = {};
    Object.keys(groupedExercises).forEach(target => {
      indexByTarget[target] = 0;
    });
    setCurrentIndexByTarget(indexByTarget);
  }

  function handleNext(target) {
    setCurrentIndexByTarget(prevIndexByTarget => ({
      ...prevIndexByTarget,
      [target]: prevIndexByTarget[target] + 1,
    }));
  }

  function handlePrev(target) {
    setCurrentIndexByTarget(prevIndexByTarget => ({
      ...prevIndexByTarget,
      [target]: prevIndexByTarget[target] - 1,
    }));
  }

  if (loading) {
    return <div>Loading...</div>;
  }

  if (error) {
    return <div>Error: {error.message}</div>;
  }

  return (
    <div>
      {Object.keys(exercisesByTarget).map(target => (
        <div key={target}>
          <h2>{target}</h2>
          <div className="card-slider">
            {exercisesByTarget[target].slice(
              currentIndexByTarget[target] * cardsPerTarget,
              (currentIndexByTarget[target] + 1) * cardsPerTarget
            ).map(exercise => (
              <div key={exercise.id} className='back-card'>
                <ExpandableCard exercise={exercise} />
              </div>
            ))}
          </div>
          <button className='card-slider-btn-prev' 
                  onClick={() => handlePrev(target)} 
                  disabled={currentIndexByTarget[target] <= 0}>
            Prev
          </button>
          <button className='card-slider-btn-next'
                  onClick={() => handleNext(target)} 
                  disabled={currentIndexByTarget[target] >= Math.ceil(exercisesByTarget[target].length / cardsPerTarget) - 1}>
            Next
          </button>
        </div>
      ))}
    </div>
  );
};

export default Chest;