import React from 'react';
// import Card from 'react-bootstrap/Card';
import './styles/ExpandableCard.css';


function ExpandableCard({ exercise }) {
console.log(exercise);
const instructions = JSON.parse(exercise.instructions);
return (
  <div id="carouselExample" class="carousel slide">
    <div class="carousel-inner">
      <div class="carousel-item active">

        <div className="card" style={{ width: '18rem' }}>
          <div className="card-body">
            <h5 class="card-title"> {exercise.name} </h5>
            <h6 class="card-subtitle mb-2 text-body-secondary"> Equipment: {exercise.equipment}</h6>
            {exercise.secondary_muscles && (
              <h6 class="card-subtitle mb-2 text-body-secondary">Secondary Muscles: {JSON.parse(exercise.secondary_muscles).join(', ')}</h6>
            )}
            <ul>
              {instructions.map((instruction, index) => (
                <li key={index} className='card-text'>{instruction}</li>
              ))}
            </ul>
            <a href='#' class="card-link">{exercise.gif_url}</a>
          </div>
        </div>
      </div>
    </div>
    <button class="carousel-control-prev" type="button" data-bs-target="#carouselExample" data-bs-slide="prev">
      <span class="carousel-control-prev-icon" aria-hidden="true"></span>
      <span class="visually-hidden">Previous</span>
    </button>
    <button class="carousel-control-next" type="button" data-bs-target="#carouselExample" data-bs-slide="next">
      <span class="carousel-control-next-icon" aria-hidden="true"></span>
      <span class="visually-hidden">Next</span>
    </button>
  </div>
)
}

export default ExpandableCard;