import React from 'react';
import { Draggable, Dropcontainer, Eventcalendar, getJson, setOptions, Toast } from '@mobiscroll/react';
import PropTypes from 'prop-types';
import { useCallback, useEffect, useMemo, useState } from 'react';
import '@mobiscroll/react/dist/css/mobiscroll.min.css'
import axios from 'axios';


function Exercise(props) {
  const [draggable, setDraggable] = useState();

  //callback to set draggable element
  const setDragElm = useCallback((elm) => {
    setDraggable(elm);
  }, []);

  const exercise = props.data;
  const durationMinutes = exercise.duration;

  return (
    <div>
      {!exercise.hide && (
        <div ref={setDragElm} className="external-drop-exercise" style={{ background: exercise.color }}>
          <div>{exercise.name}</div>
          <div>{durationMinutes + ' minute' + (durationMinutes > 1 ? 's' : '')}</div>
          <Draggable dragData={exercise} element={draggable} />
        </div>
      )}
    </div>
  );
}
//Define prop types for Exercise component
Exercise.propTypes = {
  data: PropTypes.object.isRequired,
};

function Calendar() {
  const [myEvents, setEvents] = useState([]);
  const [myExercises, setExercises] = useState([]);
  const [dropCont, setDropCont] = useState();
  const [toastText, setToastText] = useState('');
  const [isToastOpen, setIsToastOpen] = useState(false);
  const [selectedBodyPart, setSelectedBodyPart] = useState('');
  const [isLoading, setIsLoading] = useState(false);

  // Memoize calendar view options
  const myView = useMemo(
    () => ({
      schedule: { type: 'day' },
    }),
    [],
  );

  // Fetch exercises from the db based on body part
  useEffect(() => {
    async function getExercisesFromDb(bodyPart) {
      try {
        console.log('Fetching exercises for body part:', bodyPart);
        setIsLoading(true);
        const response = await axios.post('http://localhost:5000/exercises', { bodyPart: bodyPart });
        const formattedExercises = response.data.map((exercise, index) => ({
          id: index + 1,
          name: exercise.name,
          color: createRandomColor(),
          start: `2024-04-03T08:${index * 10}`,
          end: `2024-04-03T08:${index * 10}`,
        }))
        setExercises(formattedExercises);
        console.log("Fetched exercises:", formattedExercises);
        setIsLoading(false);
      } catch (err) {
        console.error('Error fetching exercises', err)
        throw err;
      }
    }

    if (selectedBodyPart) {
      console.log("Selected Body Part:", selectedBodyPart);
      getExercisesFromDb(selectedBodyPart)
    }
  }, [selectedBodyPart]);

  // Callback to set drop container element 
  const setDropElm = useCallback((elm) => {
    console.log('setDropElm', elm);
    setDropCont(elm);
  }, []);

  //Callback to handle event creation in the calendar
  const handleEventCreate = useCallback(
    (args) => {
      console.log("args:", args);
      console.log("myExercises:", myExercises);

      if (args && args.event && args.event.name) {
        console.log("args.event.name:", args.event.name);
        setExercises(myExercises.filter((item) => item.name !== args.event.name));
        console.log('handleEvent', args.event.name)
        setToastText(args.event.name + ' added');
        setIsToastOpen(true);
      } else {
        console.log('No event data provided')
      }
    },
    [myExercises],
  );
  
  // Callback to handle event deletion in the calendar 
  const handleEventDelete = useCallback((args) => {
    setToastText(args.exercise.name + ' removed');
    setIsToastOpen(true);
  }, []);

  // Callback to handle dropping an item on the calendar
  const handleItemDrop = useCallback(
    (args) => {
      console.log("handleDrop args:", args);
      console.log("HandleDrop myExercises:", myExercises);
      if (args.event && args.event.name) {
        console.log('HandleItemDrop', args.event.name)
        console.log('handleItemDrop', myExercises)
        setExercises([...myExercises, args.event]);
        console.log('handleItemDrop', setExercises)
      }
    },
    [myExercises],
  );

  // Callback to handle closing the toast
  const handleCloseToast = useCallback(() => {
    setIsToastOpen(false);
  }, []);

  // Callback to handle body part selection change
  function handleBodyPartChange(evt) {
    console.log("Selected body part changed:", evt.target.value);
    setSelectedBodyPart(evt.target.value)
  }

  // function to generate random color 
  function createRandomColor() {
    const letters = '0123456789ABCDEF';
    let color = '#';
    for (let i = 0; i < 6; i++) {
      color += letters[Math.floor(Math.random() * 16)]
    }
    return color;
  }

  return (
    <div className="mbsc-grid mbsc-no-padding">
      <div className="mbsc-row">
        <div className="mbsc-col-sm-9 external-drop-calendar">
          <Eventcalendar
            theme='windows'
            themeVariant='dark'
            data={myEvents}
            view={myView}
            dragToMove={true}
            dragToCreate={true}
            externalDrop={true}
            externalDrag={true}
            onEventCreate={handleEventCreate}
            onEventDelete={handleEventDelete}
          />
          <Toast message={toastText} isOpen={isToastOpen} onClose={handleCloseToast} />
        </div>
        <div className="mbsc-col-sm-3 external-drop-cont" ref={setDropElm}>
          <Dropcontainer onItemDrop={handleItemDrop} element={dropCont} ref={setDropElm}>
            <div className="mbsc-form-group-title">Available exercises</div>
            <select value={selectedBodyPart} onChange={handleBodyPartChange}>
              <option value="">Select Body Part</option>
              <option value="back">Back</option>
              <option value="chest">Chest</option>
              <option value="lowerArms">Lower Arms</option>
              <option value="lowerLegs">Lower Legs</option>
              <option value="neck">Neck</option>
              <option value="shoulders">Shoulders</option>
              <option value="upperArms">Upper Arms</option>
              <option value="cardio">Cardio</option>
            </select>
            {myExercises.map((exercise) => (
              <Exercise key={exercise.id} data={exercise} />
            ))}
          </Dropcontainer>
        </div>
      </div>
    </div>
  );
}

export default Calendar;




